    import puzzleBg from "../assets/img/puzzlebg.jpeg";
    import '../sass/main.scss';
    import 'reset-css';


    export default {
        name: "Tetris",
        data: function () {
            return {
                state: 'running',
                figuresAll: [
                    {
                        name: 'line',
