                        color: '#e95c26',
                        image: puzzleBg
                    },
                    {name: 'L', points: [[0, 0], [0, 1], [0, 2], [1, 2],], mode: 'rot', color: '#ff0', image: puzzleBg},
                    {
                        name: 'L reverse',
                        points: [[1, 0], [1, 1], [1, 2], [0, 2],],
                        mode: 'rot',
                        color: '#ff0',
                        image: puzzleBg
                    },
                    // http://old.absolutist.com/tetris/tetris.html
                    {
                        name: 'L',
                        points: [[0, 0], [0, 1], [0, 2], [1, 2], [2, 2],],
